export const getSpPath = (suit: string, count: number) => {
    let letter: string = ''
    switch (suit) {
        case 'HongTao':
            letter = 'H'
            break;
        case 'HeiTao':
            letter = 'S'
            break;
        case 'MeiHua':
            letter = 'C'
            break;
        case 'FangKuai':
            letter = 'D'
            break;
    }

    return `Texture/Cards/${suit}/${count}${letter}`
}